//

import Component from '../material-base/component';
import SimpleMenuFoundation from './foundation-simple';
import { getTransformPropertyName } from './util';


export { SimpleMenuFoundation };


export class SimpleMenu extends Component {
  static attachTo(root) {
    return new SimpleMenu(root);
  }

  constructor(...args) {
    super(...args);
    this.previousFocus;  // eslint-disable-line no-unused-expressions
  }

  get open() {
    return this.foundationEl.isOpen();
  }

  set open(value) {
    if (value) {
      this.foundationEl.open();
    } else {
      this.foundationEl.close();
    }
  }

  get itemsContainerV() {
    return this.rootEl.querySelector(SimpleMenuFoundation.strings.ITEMS_SELECTOR);
  }

  get items() {
    const { itemsContainerV: itemsContainer } = this;
    return [].slice.call(itemsContainer.querySelectorAll('.list-item[role]'));
  }

  getDefaultFoundation() {
    return new SimpleMenuFoundation({
      addClass: className => this.rootEl.classList.add(className),
      removeClass: className => this.rootEl.classList.remove(className),
      hasClass: className => this.rootEl.classList.contains(className),
      hasNecessaryDom: () => Boolean(this.itemsContainerV),
      getAttributeForEventTarget: (target, attributeName) => target.getAttribute(attributeName),
      getInnerDimensions: () => {
        const { itemsContainerV: itemsContainer } = this;
        return { width: itemsContainer.offsetWidth, height: itemsContainer.offsetHeight };
      },
      hasAnchor: () => this.rootEl.parentElement && this.rootEl.parentElement.classList.contains('menu-anchor'),
      getAnchorDimensions: () => this.rootEl.parentElement.getBoundingClientRect(),
      getWindowDimensions: () => ({ width: window.innerWidth, height: window.innerHeight }),
      setScale: (x, y) => {
        this.rootEl.style[getTransformPropertyName(window)] = `scale(${x}, ${y})`;
      },
      setInnerScale: (x, y) => {
        this.itemsContainerV.style[getTransformPropertyName(window)] = `scale(${x}, ${y})`;
      },
      getNumberOfItems: () => this.items.length,
      registerInteractionHandler: (type, handler) => this.rootEl.addEventListener(type, handler),
      deregisterInteractionHandler: (type, handler) =>
        this.rootEl.removeEventListener(type, handler),
      registerBodyClickHandler: handler => document.body.addEventListener('click', handler),
      deregisterBodyClickHandler: handler => document.body.removeEventListener('click', handler),
      getYParamsForItemAtIndex: (index) => {
        const { offsetTop: top, offsetHeight: height } = this.items[index];
        return { top, height };
      },
      setTransitionDelayForItemAtIndex: (index, value) =>
        this.items[index].style.setProperty('transition-delay', value),
      getIndexForEventTarget: target => this.items.indexOf(target),
      notifySelected: evtData => this.emit(SimpleMenuFoundation.strings.SELECTED_EVENT, {
        index: evtData.index,
        item: this.items[evtData.index],
      }),
      notifyCancel: () => this.emit(SimpleMenuFoundation.strings.CANCEL_EVENT, {}),
      saveFocus: () => {
        this.previousFocus = document.activeElement;
      },
      restoreFocus: () => {
        if (this.previousFocus) {
          this.previousFocus.focus();
        }
      },
      isFocused: () => document.activeElement === this.rootEl,
      focus: () => this.rootEl.focus(),
      getFocusedItemIndex: () => this.items.indexOf(document.activeElement),
      focusItemAtIndex: index => this.items[index].focus(),
      isRtl: () => getComputedStyle(this.rootEl).getPropertyValue('direction') === 'rtl',
      setTransformOrigin: (origin) => {
        this.rootEl.style[`${getTransformPropertyName(window)}-origin`] = origin;
      },
      setPosition: (position) => {
        this.rootEl.style.left = 'left' in position ? position.left : null;
        this.rootEl.style.right = 'right' in position ? position.right : null;
        this.rootEl.style.top = 'top' in position ? position.top : null;
        this.rootEl.style.bottom = 'bottom' in position ? position.bottom : null;
      },
      getAccurateTime: () => window.performance.now(),
    });
  }

  show({ focusIndex = null } = {}) {
    this.foundationEl.open({ focusIndex });
  }

  hide() {
    this.foundationEl.close();
  }
}
