//

import Component from '../material-base/component';
import { SimpleMenu } from './index-simple';

import SelectFoundation from './foundation';

export { SelectFoundation };

export class Select extends Component {
  static attachTo(root) {
    return new Select(root);
  }

  get value() {
    return this.foundationEl.getValue();
  }

  get options() {
    return this.menuV.items;
  }

  get selectedOptions() {
    return this.rootEl.querySelectorAll('[aria-selected]');
  }

  get selectedIndex() {
    return this.foundationEl.getSelectedIndex();
  }

  set selectedIndex(selectedIndex) {
    this.foundationEl.setSelectedIndex(selectedIndex);
  }

  get disabled() {
    return this.foundationEl.isDisabled();
  }

  set disabled(disabled) {
    this.foundationEl.setDisabled(disabled);
  }

  getDefaultFoundation() {
    return new SelectFoundation({
      addClass: className =>
          this.rootEl.classList.add(className),
      removeClass: className =>
          this.rootEl.classList.remove(className),
      setAttr: (attr, value) =>
          this.rootEl.setAttribute(attr, value),
      rmAttr: (attr, value) =>
          this.rootEl.removeAttribute(attr, value),
      computeBoundingRect: () =>
          this.rootEl.getBoundingClientRect(),
      registerInteractionHandler: (type, handler) =>
          this.rootEl.addEventListener(type, handler),
      deregisterInteractionHandler: (type, handler) =>
          this.rootEl.removeEventListener(type, handler),
      focus: () =>
          this.rootEl.focus(),
      makeTabbable: () => {
        this.rootEl.tabIndex = 0;
      },
      makeUntabbable: () => {
        this.rootEl.tabIndex = -1;
      },
      getComputedStyleValue: prop =>
          window.getComputedStyle(this.rootEl).getPropertyValue(prop),
      setStyle: (propertyName, value) =>
          this.rootEl.style.setProperty(propertyName, value),
      create2dRenderingContext: () =>
          document.createElement('canvas').getContext('2d'),
      setMenuElStyle: (propertyName, value) =>
          this.menuElV.style.setProperty(propertyName, value),
      setMenuElAttr: (attr, value) =>
          this.menuElV.setAttribute(attr, value),
      rmMenuElAttr: attr =>
          this.menuElV.removeAttribute(attr),
      getMenuElOffsetHeight: () =>
          this.menuElV.offsetHeight,
      openMenu: focusIndex =>
          this.menuV.show({ focusIndex }),
      isMenuOpen: () =>
          this.menuV.open,
      setSelectedTextContent: (selectedTextContent) => {
        this.selectedTextV.textContent = selectedTextContent;
      },
      getNumberOfOptions: () =>
          this.options.length,
      getTextForOptionAtIndex: index =>
          this.options[index].textContent,
      getValueForOptionAtIndex: index =>
          this.options[index].id || this.options[index].textContent,
      setAttrForOptionAtIndex: (index, attr, value) =>
          this.options[index].setAttribute(attr, value),
      rmAttrForOptionAtIndex: (index, attr) =>
          this.options[index].removeAttribute(attr),
      getOffsetTopForOptionAtIndex: index =>
          this.options[index].offsetTop,
      registerMenuInteractionHandler: (type, handler) =>
          this.menuV.listen(type, handler),
      deregisterMenuInteractionHandler: (type, handler) =>
          this.menuV.unlisten(type, handler),
      notifyChange: () =>
          this.emit(SelectFoundation.strings.CHANGE_EVENT, this),
      getWindowInnerHeight: () =>
          window.innerHeight,
    });
  }

  item(index) {
    return this.options[index] || null;
  }

  nameditem(key) {
    // NOTE: IE11 precludes us from using Array.prototype.find
    // eslint-disable-next-line no-cond-assign
    for (let i = 0, options = this.options, option; (option = options[i]); i += 1) {
      if (option.id === key || option.getAttribute('name') === key) {
        return option;
      }
    }
    return null;
  }

  initialize(menuFactory = el => new SimpleMenu(el)) {
    this.menuElV = this.rootEl.querySelector('.select__menu');
    this.menuV = menuFactory(this.menuElV);
    this.selectedTextV = this.rootEl.querySelector('.select__selected-text');
  }

  initialSyncWithDOM() {
    const selectedOption = this.selectedOptions[0];
    const idx = selectedOption ? this.options.indexOf(selectedOption) : -1;
    if (idx >= 0) {
      this.selectedIndex = idx;
    }

    if (this.rootEl.getAttribute('aria-disabled') === 'true') {
      this.disabled = true;
    }
  }
}
