//

import Foundation from '../material-base/foundation';
import { cssClasses, strings } from './constants';
import SimpleMenuFoundation from './foundation-simple';

const OPENER_KEYS = [
  { key: 'ArrowUp', keyCode: 38, forType: 'keydown' },
  { key: 'ArrowDown', keyCode: 40, forType: 'keydown' },
  { key: 'Space', keyCode: 32, forType: 'keyup' },
];

export default class SelectFoundation extends Foundation {
  static get cssClasses() {
    return cssClasses;
  }

  static get strings() {
    return strings;
  }

  static get defaultAdapter() {
    return {
      addClass: () => {},
      removeClass: () => {},
      setAttr: () => {},
      rmAttr: () => {},
      computeBoundingRect: () => ({ left: 0, top: 0 }),
      registerInteractionHandler: () => {},
      deregisterInteractionHandler: () => {},
      focus: () => {},
      makeTabbable: () => {},
      makeUntabbable: () => {},
      getComputedStyleValue: () => '',
      setStyle: () => {},
      create2dRenderingContext: () => ({
        font: '',
        measureText: () => ({ width: 0 }),
      }),
      setMenuElStyle: () => {},
      setMenuElAttr: () => {},
      rmMenuElAttr: () => {},
      getMenuElOffsetHeight: () => 0,
      openMenu: () => {},
      isMenuOpen: () => false,
      setSelectedTextContent: () => {},
      getNumberOfOptions: () => 0,
      getTextForOptionAtIndex: () => '',
      getValueForOptionAtIndex: () => '',
      setAttrForOptionAtIndex: () => {},
      rmAttrForOptionAtIndex: () => {},
      getOffsetTopForOptionAtIndex: () => 0,
      registerMenuInteractionHandler: () => {},
      deregisterMenuInteractionHandler: () => {},
      notifyChange: () => {},
      getWindowInnerHeight: () => 0,
    };
  }

  constructor(adapter) {
    super(Object.assign(SelectFoundation.defaultAdapter, adapter));
    this.ctx = null;
    this.selectedIndex = -1;
    this.disabledV = false;
    this.displayHandler = (evt) => {
      evt.preventDefault();
      if (!this.adapter.isMenuOpen()) {
        this.openFn();
      }
    };
    this.displayViaKeyboardHandler = evt => this.handleDisplayViaKeyboard(evt);
    this.selectionHandler = ({ detail }) => {
      const { index } = detail;
      this.closeFn();
      if (index !== this.selectedIndex) {
        this.setSelectedIndex(index);
        this.adapter.notifyChange();
      }
    };
    this.cancelHandler = () => {
      this.closeFn();
    };
  }

  init() {
    this.ctx = this.adapter.create2dRenderingContext();
    this.adapter.registerInteractionHandler('click', this.displayHandler);
    this.adapter.registerInteractionHandler('keydown', this.displayViaKeyboardHandler);
    this.adapter.registerInteractionHandler('keyup', this.displayViaKeyboardHandler);
    this.adapter.registerMenuInteractionHandler(
      SimpleMenuFoundation.strings.SELECTED_EVENT,
      this.selectionHandler  // eslint-disable-line comma-dangle
    );
    this.adapter.registerMenuInteractionHandler(
      SimpleMenuFoundation.strings.CANCEL_EVENT, this.cancelHandler);
    this.resize();
  }

  destroy() {
    // Drop reference to context object to prevent potential leaks
    this.ctx = null;
    this.adapter.deregisterInteractionHandler('click', this.displayHandler);
    this.adapter.deregisterInteractionHandler('keydown', this.displayViaKeyboardHandler);
    this.adapter.deregisterInteractionHandler('keyup', this.displayViaKeyboardHandler);
    this.adapter.deregisterMenuInteractionHandler(
      SimpleMenuFoundation.strings.SELECTED_EVENT, this.selectionHandler);
    this.adapter.deregisterMenuInteractionHandler(
      // eslint-disable-next-line comma-dangle
      SimpleMenuFoundation.strings.CANCEL_EVENT, this.cancelHandler
    );
  }

  getValue() {
    return this.selectedIndex >= 0 ? this.adapter.getValueForOptionAtIndex(this.selectedIndex) : '';
  }

  getSelectedIndex() {
    return this.selectedIndex;
  }

  setSelectedIndex(index) {
    const prevSelectedIndex = this.selectedIndex;
    if (prevSelectedIndex >= 0) {
      this.adapter.rmAttrForOptionAtIndex(this.selectedIndex, 'aria-selected');
    }

    this.selectedIndex = index >= 0 && index < this.adapter.getNumberOfOptions() ? index : -1;
    let selectedTextContent = '';
    if (this.selectedIndex >= 0) {
      selectedTextContent = this.adapter.getTextForOptionAtIndex(this.selectedIndex).trim();
      this.adapter.setAttrForOptionAtIndex(this.selectedIndex, 'aria-selected', 'true');
    }
    this.adapter.setSelectedTextContent(selectedTextContent);
  }

  isDisabled() {
    return this.disabledV;
  }

  setDisabled(disabled) {
    const { DISABLED } = SelectFoundation.cssClasses;
    this.disabledV = disabled;
    if (this.disabledV) {
      this.adapter.addClass(DISABLED);
      this.adapter.setAttr('aria-disabled', 'true');
      this.adapter.makeUntabbable();
    } else {
      this.adapter.removeClass(DISABLED);
      this.adapter.rmAttr('aria-disabled');
      this.adapter.makeTabbable();
    }
  }

  resize() {
    const font = this.adapter.getComputedStyleValue('font');
    const letterSpacing = parseFloat(this.adapter.getComputedStyleValue('letter-spacing'));
    if (font) {
      this.ctx.font = font;
    } else {
      const primaryFontFamily = this.adapter.getComputedStyleValue('font-family').split(',')[0];
      const fontSize = this.adapter.getComputedStyleValue('font-size');
      this.ctx.font = `${fontSize} ${primaryFontFamily}`;
    }

    let maxTextLength = 0;
    for (let i = 0, l = this.adapter.getNumberOfOptions(); i < l; i += 1) {
      const txt = this.adapter.getTextForOptionAtIndex(i).trim();
      const { width } = this.ctx.measureText(txt);
      const addedSpace = letterSpacing * txt.length;
      maxTextLength = Math.max(maxTextLength, Math.ceil(width + addedSpace));
    }
    this.adapter.setStyle('width', `${maxTextLength}px`);
  }

  openFn() {
    const { OPEN } = SelectFoundation.cssClasses;
    const focusIndex = this.selectedIndex < 0 ? 0 : this.selectedIndex;

    this.setMenuStylesForOpenAtIndex(focusIndex);
    this.adapter.addClass(OPEN);
    this.adapter.openMenu(focusIndex);
  }

  setMenuStylesForOpenAtIndex(index) {
    const innerHeight = this.adapter.getWindowInnerHeight();
    const { left, top } = this.adapter.computeBoundingRect();

    this.adapter.setMenuElAttr('aria-hidden', 'true');
    this.adapter.setMenuElStyle('display', 'block');
    const menuHeight = this.adapter.getMenuElOffsetHeight();
    const itemOffsetTop = this.adapter.getOffsetTopForOptionAtIndex(index);
    this.adapter.setMenuElStyle('display', '');
    this.adapter.rmMenuElAttr('aria-hidden');

    let adjustedTop = top - itemOffsetTop;
    const overflowsTop = adjustedTop < 0;
    const overflowsBottom = adjustedTop + menuHeight > innerHeight;
    if (overflowsTop) {
      adjustedTop = 0;
    } else if (overflowsBottom) {
      adjustedTop = Math.max(0, innerHeight - menuHeight);
    }

    this.adapter.setMenuElStyle('left', `${left}px`);
    this.adapter.setMenuElStyle('top', `${adjustedTop}px`);
    this.adapter.setMenuElStyle('transform-origin', `center ${itemOffsetTop}px`);
  }

  closeFn() {
    const { OPEN } = SelectFoundation.cssClasses;
    this.adapter.removeClass(OPEN);
    this.adapter.focus();
  }

  handleDisplayViaKeyboard(evt) {
    // We use a hard-coded 2 instead of Event.AT_TARGET to avoid having to reference a browser
    // global.
    const EVENT_PHASE_AT_TARGET = 2;
    if (evt.eventPhase !== EVENT_PHASE_AT_TARGET) {
      return;
    }

    // Prevent pressing space down from scrolling the page
    const isSpaceDown = evt.type === 'keydown' && (evt.key === 'Space' || evt.keyCode === 32);
    if (isSpaceDown) {
      evt.preventDefault();
    }

    const isOpenerKey = OPENER_KEYS.some(
      ({ key, keyCode, forType }) => (
        evt.type === forType && (evt.key === key || evt.keyCode === keyCode)
      )  // eslint-disable-line comma-dangle
    );
    if (isOpenerKey) {
      this.displayHandler(evt);
    }
  }
}
