//

import Foundation from '../material-base/foundation';
// import SimpleMenuAdapter from './adapter-simple';
import { cssClasses, strings, numbers } from './constants-simple';
import { clamp, bezierProgress } from './util';

export default class SimpleMenuFoundation extends Foundation {
  static get cssClasses() {
    return cssClasses;
  }

  static get strings() {
    return strings;
  }

  static get numbers() {
    return numbers;
  }

  static get defaultAdapter() {
    return ({
      addClass: () => {},
      removeClass: () => {},
      hasClass: () => false,
      hasNecessaryDom: () => false,
      getAttributeForEventTarget: () => {},
      getInnerDimensions: () => ({}),
      hasAnchor: () => false,
      getAnchorDimensions: () => ({}),
      getWindowDimensions: () => ({}),
      setScale: () => {},
      setInnerScale: () => {},
      getNumberOfItems: () => 0,
      registerInteractionHandler: () => {},
      deregisterInteractionHandler: () => {},
      registerBodyClickHandler: () => {},
      deregisterBodyClickHandler: () => {},
      getYParamsForItemAtIndex: () => ({}),
      setTransitionDelayForItemAtIndex: () => {},
      getIndexForEventTarget: () => 0,
      notifySelected: () => {},
      notifyCancel: () => {},
      saveFocus: () => {},
      restoreFocus: () => {},
      isFocused: () => false,
      focus: () => {},
      getFocusedItemIndex: () => -1,
      focusItemAtIndex: () => {},
      isRtl: () => false,
      setTransformOrigin: () => {},
      setPosition: () => {},
      getAccurateTime: () => 0,
    });
  }

  constructor(adapter) {
    super(Object.assign(SimpleMenuFoundation.defaultAdapter, adapter));

    this.clickHandlerFn = evt => this.handlePossibleSelected(evt);
    this.keydownHandlerFn = evt => this.handleKeyboardDown(evt);
    this.keyupHandlerFn = evt => this.handleKeyboardUp(evt);
    this.documentClickHandlerFn = (evt) => {
      this.adapter.notifyCancel();
      this.close(evt);
    };
    this.isOpenV = false;
    this.startScaleX = 0;
    this.startScaleYV = 0;
    this.targetScale = 1;
    this.scaleX = 0;
    this.scaleY = 0;
    this.runningV = false;
    this.selectedTriggerTimerId = 0;
    this.animationRequestId = 0;
    this.dimensionsV;  // eslint-disable-line no-unused-expressions
    this.startTime;  // eslint-disable-line no-unused-expressions
    this.itemHeight;  // eslint-disable-line no-unused-expressions
  }

  init() {
    const { ROOT, OPEN } = SimpleMenuFoundation.cssClasses;

    if (!this.adapter.hasClass(ROOT)) {
      throw new Error(`${ROOT} class required in root element.`);
    }

    if (!this.adapter.hasNecessaryDom()) {
      throw new Error(`Required DOM nodes missing in ${ROOT} component.`);
    }

    if (this.adapter.hasClass(OPEN)) {
      this.isOpenV = true;
    }

    this.adapter.registerInteractionHandler('click', this.clickHandlerFn);
    this.adapter.registerInteractionHandler('keyup', this.keyupHandlerFn);
    this.adapter.registerInteractionHandler('keydown', this.keydownHandlerFn);
  }

  destroy() {
    clearTimeout(this.selectedTriggerTimerId);
    // Cancel any currently running animations.
    cancelAnimationFrame(this.animationRequestId);
    this.adapter.deregisterInteractionHandler('click', this.clickHandlerFn);
    this.adapter.deregisterInteractionHandler('keyup', this.keyupHandlerFn);
    this.adapter.deregisterInteractionHandler('keydown', this.keydownHandlerFn);
    this.adapter.deregisterBodyClickHandler(this.documentClickHandlerFn);
  }

  applyTransitionDelays() {
    const { BOTTOM_LEFT, BOTTOM_RIGHT } = SimpleMenuFoundation.cssClasses;
    const numItems = this.adapter.getNumberOfItems();
    const { height } = this.dimensionsV;
    const transitionDuration = SimpleMenuFoundation.numbers.TRANSITION_DURATION_MS / 1000;
    const start = SimpleMenuFoundation.numbers.TRANSITION_SCALE_ADJUSTMENT_Y;

    for (let index = 0; index < numItems; index += 1) {
      const { top: itemTop, height: itemHeight } = this.adapter.getYParamsForItemAtIndex(index);
      this.itemHeight = itemHeight;
      let itemDelayFraction = itemTop / height;
      if (this.adapter.hasClass(BOTTOM_LEFT) || this.adapter.hasClass(BOTTOM_RIGHT)) {
        itemDelayFraction = ((height - itemTop - itemHeight) / height);
      }
      const itemDelay = (start + (itemDelayFraction * (1 - start))) * transitionDuration;
      // Use toFixed() here to normalize CSS unit precision across browsers
      this.adapter.setTransitionDelayForItemAtIndex(index, `${itemDelay.toFixed(3)}s`);
    }
  }

  removeTransitionDelays() {
    const numItems = this.adapter.getNumberOfItems();
    for (let i = 0; i < numItems; i += 1) {
      this.adapter.setTransitionDelayForItemAtIndex(i, null);
    }
  }


  animationLoop() {
    const time = this.adapter.getAccurateTime();
    const {
      TRANSITION_DURATION_MS,
      TRANSITION_X1,
      TRANSITION_Y1,
      TRANSITION_X2,
      TRANSITION_Y2,
      TRANSITION_SCALE_ADJUSTMENT_X,
      TRANSITION_SCALE_ADJUSTMENT_Y,
    } = SimpleMenuFoundation.numbers;
    const currentTime = clamp((time - this.startTime) / TRANSITION_DURATION_MS);

    // Animate X axis very slowly, so that only the Y axis animation is visible during fade-out.
    let currentTimeX = clamp(
      // eslint-disable-next-line comma-dangle
      (currentTime - TRANSITION_SCALE_ADJUSTMENT_X) / (1 - TRANSITION_SCALE_ADJUSTMENT_X)
    );
    // No time-shifting on the Y axis when closing.
    let currentTimeY = currentTime;

    let startScaleY = this.startScaleYV;
    if (this.targetScale === 1) {
      // Start with the menu at the height of a single item.
      if (this.itemHeight) {
        startScaleY = Math.max(this.itemHeight / this.dimensionsV.height, startScaleY);
      }
      // X axis moves faster, so time-shift forward.
      currentTimeX = clamp(currentTime + TRANSITION_SCALE_ADJUSTMENT_X);
      // Y axis moves slower, so time-shift backwards and adjust speed by the difference.
      currentTimeY = clamp(
        // eslint-disable-next-line comma-dangle
        (currentTime - TRANSITION_SCALE_ADJUSTMENT_Y) / (1 - TRANSITION_SCALE_ADJUSTMENT_Y)
      );
    }

    // Apply cubic bezier easing independently to each axis.
    const easeX = bezierProgress(
      currentTimeX,
      TRANSITION_X1,
      TRANSITION_Y1,
      TRANSITION_X2,
      TRANSITION_Y2  // eslint-disable-line comma-dangle
    );
    const easeY = bezierProgress(
      currentTimeY,
      TRANSITION_X1,
      TRANSITION_Y1,
      TRANSITION_X2,
      TRANSITION_Y2  // eslint-disable-line comma-dangle
    );

    // Calculate the scales to apply to the outer container and inner container.
    this.scaleX = this.startScaleX + ((this.targetScale - this.startScaleX) * easeX);
    const invScaleX = 1 / (this.scaleX === 0 ? 1 : this.scaleX);
    this.scaleY = startScaleY + ((this.targetScale - startScaleY) * easeY);
    const invScaleY = 1 / (this.scaleY === 0 ? 1 : this.scaleY);

    // Apply scales.
    this.adapter.setScale(this.scaleX, this.scaleY);
    this.adapter.setInnerScale(invScaleX, invScaleY);

    // Stop animation when we've covered the entire 0 - 1 range of time.
    if (currentTime < 1) {
      this.animationRequestId = requestAnimationFrame(() => this.animationLoop());
    } else {
      this.animationRequestId = 0;
      this.runningV = false;
      this.adapter.removeClass(SimpleMenuFoundation.cssClasses.ANIMATING);
    }
  }

  animateMenu() {
    this.startTime = this.adapter.getAccurateTime();
    this.startScaleX = this.scaleX;
    this.startScaleYV = this.scaleY;

    this.targetScale = this.isOpenV ? 1 : 0;

    if (!this.runningV) {
      this.runningV = true;
      this.animationRequestId = requestAnimationFrame(() => this.animationLoop());
    }
  }

  focusOnOpen(focusIndex) {
    if (focusIndex === null) {
      // First, try focusing the menu.
      this.adapter.focus();
      // If that doesn't work, focus first item instead.
      if (!this.adapter.isFocused()) {
        this.adapter.focusItemAtIndex(0);
      }
    } else {
      this.adapter.focusItemAtIndex(focusIndex);
    }
  }

  handleKeyboardDown(evt) {
    // Do nothing if Alt, Ctrl or Meta are pressed.
    if (evt.altKey || evt.ctrlKey || evt.metaKey) {
      return true;
    }

    const { keyCode, key, shiftKey } = evt;
    const isTab = key === 'Tab' || keyCode === 9;
    const isArrowUp = key === 'ArrowUp' || keyCode === 38;
    const isArrowDown = key === 'ArrowDown' || keyCode === 40;
    const isSpace = key === 'Space' || keyCode === 32;

    const focusedItemIndex = this.adapter.getFocusedItemIndex();
    const lastItemIndex = this.adapter.getNumberOfItems() - 1;

    if (shiftKey && isTab && focusedItemIndex === 0) {
      this.adapter.focusItemAtIndex(lastItemIndex);
      evt.preventDefault();
      return false;
    }

    if (!shiftKey && isTab && focusedItemIndex === lastItemIndex) {
      this.adapter.focusItemAtIndex(0);
      evt.preventDefault();
      return false;
    }

    // Ensure Arrow{Up,Down} and space do not cause inadvertent scrolling
    if (isArrowUp || isArrowDown || isSpace) {
      evt.preventDefault();
    }

    if (isArrowUp) {
      if (focusedItemIndex === 0 || this.adapter.isFocused()) {
        this.adapter.focusItemAtIndex(lastItemIndex);
      } else {
        this.adapter.focusItemAtIndex(focusedItemIndex - 1);
      }
    } else if (isArrowDown) {
      if (focusedItemIndex === lastItemIndex || this.adapter.isFocused()) {
        this.adapter.focusItemAtIndex(0);
      } else {
        this.adapter.focusItemAtIndex(focusedItemIndex + 1);
      }
    }

    return true;
  }

  handleKeyboardUp(evt) {
    // Do nothing if Alt, Ctrl or Meta are pressed.
    if (evt.altKey || evt.ctrlKey || evt.metaKey) {
      return true;
    }

    const { keyCode, key } = evt;
    const isEnter = key === 'Enter' || keyCode === 13;
    const isSpace = key === 'Space' || keyCode === 32;
    const isEscape = key === 'Escape' || keyCode === 27;

    if (isEnter || isSpace) {
      this.handlePossibleSelected(evt);
    }

    if (isEscape) {
      this.adapter.notifyCancel();
      this.close();
    }

    return true;
  }

  handlePossibleSelected(evt) {
    if (this.adapter.getAttributeForEventTarget(evt.target, strings.ARIA_DISABLED_ATTR) === 'true') {
      return;
    }
    const targetIndex = this.adapter.getIndexForEventTarget(evt.target);
    if (targetIndex < 0) {
      return;
    }
    // Debounce multiple selections
    if (this.selectedTriggerTimerId) {
      return;
    }
    this.selectedTriggerTimerId = setTimeout(() => {
      this.selectedTriggerTimerId = 0;
      this.close();
      this.adapter.notifySelected({ index: targetIndex });
    }, numbers.SELECTED_TRIGGER_DELAY);
  }

  autoPosition() {
    if (!this.adapter.hasAnchor()) {
      return;
    }

    // Defaults: open from the top left.
    let vertical = 'top';
    let horizontal = 'left';

    const anchor = this.adapter.getAnchorDimensions();
    const windowDimensions = this.adapter.getWindowDimensions();

    const topOverflow = (anchor.top + this.dimensionsV.height) - windowDimensions.height;
    const bottomOverflow = this.dimensionsV.height - anchor.bottom;
    const extendsBeyondTopBounds = topOverflow > 0;

    if (extendsBeyondTopBounds) {
      if (bottomOverflow < topOverflow) {
        vertical = 'bottom';
      }
    }

    const leftOverflow = (anchor.left + this.dimensionsV.width) - windowDimensions.width;
    const rightOverflow = this.dimensionsV.width - anchor.right;
    const extendsBeyondLeftBounds = leftOverflow > 0;
    const extendsBeyondRightBounds = rightOverflow > 0;

    if (this.adapter.isRtl()) {
      // In RTL, we prefer to open from the right.
      horizontal = 'right';
      if (extendsBeyondRightBounds && leftOverflow < rightOverflow) {
        horizontal = 'left';
      }
    } else if (extendsBeyondLeftBounds && rightOverflow < leftOverflow) {
      horizontal = 'right';
    }

    const position = {
      [horizontal]: '0',
      [vertical]: '0',
    };

    this.adapter.setTransformOrigin(`${vertical} ${horizontal}`);
    this.adapter.setPosition(position);
  }


  open({ focusIndex = null } = {}) {
    this.adapter.saveFocus();
    this.adapter.addClass(SimpleMenuFoundation.cssClasses.ANIMATING);
    this.animationRequestId = requestAnimationFrame(() => {
      this.dimensionsV = this.adapter.getInnerDimensions();
      this.applyTransitionDelays();
      this.autoPosition();
      this.animateMenu();
      this.adapter.addClass(SimpleMenuFoundation.cssClasses.OPEN);
      this.focusOnOpen(focusIndex);
      this.adapter.registerBodyClickHandler(this.documentClickHandlerFn);
    });
    this.isOpenV = true;
  }

  close(evt = null) {
    const targetIsDisabled = evt ?
      this.adapter.getAttributeForEventTarget(evt.target, strings.ARIA_DISABLED_ATTR) === 'true' :
      false;

    if (targetIsDisabled) {
      return;
    }

    this.adapter.deregisterBodyClickHandler(this.documentClickHandlerFn);
    this.adapter.addClass(SimpleMenuFoundation.cssClasses.ANIMATING);
    requestAnimationFrame(() => {
      this.removeTransitionDelays();
      this.animateMenu();
      this.adapter.removeClass(SimpleMenuFoundation.cssClasses.OPEN);
    });
    this.isOpenV = false;
    this.adapter.restoreFocus();
  }

  /** @return {boolean} */
  isOpen() {
    return this.isOpenV;
  }
}
