import $ from 'jquery';
import _ from 'lodash';


const defaultConfig = {
  recordsPerPage: 15,
  containers: {
    pagedList: '.paginated-lists',
    pageLinks: '',
  },
  destListClass: 'tabular-list',
  initialPage: 1,
  containerType: 'list',
};

const containerTypes = {
  list: 'li',
  table: 'tbody tr',
};

const destElTypes = {
  list: 'ul',
  table: 'table',
};


export { defaultConfig };

export class Pagination {
  constructor(rawConfig) {
    const config = _.defaultsDeep(rawConfig, defaultConfig);

    _.forOwn(config, (val, key) => { this[key] = val; });

    this.$rootListEl = $(this.rootList);

    this.containerEl = (
      _.has(containerTypes, this.containerType)
    ) ? (
      containerTypes[this.containerType]
    ) : (
      ''
    );

    this.destEl = (
      _.has(destElTypes, this.containerType)
    ) ? (
      destElTypes[this.containerType]
    ) : (
      ''
    );

    const allRows = this.$rootListEl.find(`${this.containerEl}`);

    this.totalRecords = allRows.length;

    this.pages = _.chunk(
      allRows,
      this.recordsPerPage  // eslint-disable-line comma-dangle
    );

    this.events = {
      pageLinkClick: (e) => { this.showPage($(e.currentTarget).data('page')); },
      prevTriggerClick: (e) => {
        if (!$(e.currentTarget).hasClass('disabled')) {
          const newPage = Math.max((this.currentPage - 1), 1);
          this.showPage(newPage);
        }
      },
      nextTriggerClick: (e) => {
        if (!$(e.currentTarget).hasClass('disabled')) {
          const newPage = Math.min((this.currentPage + 1), this.pages.length);
          this.showPage(newPage);
        }
      },
    };
  }

  initialize() {
    this.drawPages();

    if (_.has(this.containers, 'topPageLinks')) { this.drawTopPagination(); }
    if (_.has(this.containers, 'pageLinks')) { this.drawPaginationLinks(); }
  }

  drawPages() {
    this.pages.forEach((page, i) => {
      const counter = i + 1;
      const shownClass = (counter === this.initialPage) ? 'shown' : '';

      const pageList = $(`<${this.destEl} id="paginated-${counter}" class="${
        this.destListClass
      } ${
        shownClass
      }"></${this.destEl}>`);

      if (this.containerType === 'table') {
        const pageHeaderRow = this.$rootListEl.find(`> ${this.destEl} thead`).first().clone();
        pageHeaderRow.appendTo(pageList);

        const pageInner = $('<tbody></tbody>');
        page.forEach((item) => { pageInner.append($(item).clone()); });
        pageInner.appendTo(pageList);
      } else {
        page.forEach((item) => { pageList.append($(item).clone()); });
      }

      pageList.appendTo(this.$rootListEl.find(this.containers.pagedList));
    });

    this.currentPage = this.initialPage;
  }

  drawTopPagination() {
    const topPageLinks = this.generatePagination();

    topPageLinks.appendTo(this.$rootListEl.find(this.containers.topPageLinks));
  }

  drawPaginationLinks() {
    const pageLinks = this.generatePagination();

    pageLinks.appendTo(this.$rootListEl.find(this.containers.pageLinks));

    this.updateLinkActiveness(this.currentPage);
  }

  generatePagination() {
    const pageLinks = $('<ul class="pagination-page-links"></ul>');

    const prevPageTrigger = $('<li class="prev-page">' +
        '<i class="material-icons">keyboard_arrow_left</i>' +
    '</li>');
    prevPageTrigger.on('click', this.events.prevTriggerClick);
    pageLinks.append(prevPageTrigger);

    _.range(1, (this.pages.length + 1)).forEach((pageNum) => {
      const pageLink = $(`<li class="page-nav" data-page="${pageNum}">${pageNum}</li>`);
      pageLink.on('click', this.events.pageLinkClick);
      pageLinks.append(pageLink);
    });

    const nextPageTrigger = $('<li class="next-page">' +
        '<i class="material-icons">keyboard_arrow_right</i>' +
    '</li>');
    nextPageTrigger.on('click', this.events.nextTriggerClick);
    pageLinks.append(nextPageTrigger);

    return pageLinks;
  }

  updateLinkActiveness(newPage) {
    const prevLink = this.$rootListEl.find('.prev-page');
    const nextLink = this.$rootListEl.find('.next-page');

    this.$rootListEl.find('.page-nav').removeClass('active');
    this.$rootListEl.find(`.page-nav[data-page="${newPage}"]`).addClass('active');

    if (newPage === 1) {
      if (!prevLink.hasClass('disabled')) { prevLink.addClass('disabled'); }
    } else if (prevLink.hasClass('disabled')) {
      prevLink.removeClass('disabled');
    }

    if (newPage === this.pages.length) {
      if (!nextLink.hasClass('disabled')) { nextLink.addClass('disabled'); }
    } else if (nextLink.hasClass('disabled')) {
      nextLink.removeClass('disabled');
    }
  }

  showPage(pageNumber) {
    let trimmedPageNumber = Math.min(pageNumber, (this.pages.length));
    trimmedPageNumber = Math.max(trimmedPageNumber, 1);

    if (trimmedPageNumber !== this.currentPage) {
      const newPage = this.$rootListEl.find(`#paginated-${trimmedPageNumber}`);

      if (!newPage.hasClass('shown')) {
        this.$rootListEl.find(`.${this.destListClass}`).removeClass('shown');
        newPage.addClass('shown');
        this.currentPage = trimmedPageNumber;

        this.updateLinkActiveness(trimmedPageNumber);
      }

      if (typeof this.onPageChange === 'function') {
        this.onPageChange(trimmedPageNumber);
      }
    }
  }
}
