import $ from 'jquery';


import { Select } from './index';


const buildManagedSelects = (i, el) => {
  const $originalSelect = $(el);

  const $selectManager = $originalSelect.parent();
  $selectManager.addClass('select-manager');

  const $richSelect = $('<div class="select" role="listbox" tabindex="0"></div>');

  const $richActiveText = $('<span class="select__selected-text"></span>');
  $richActiveText.text($originalSelect.find('option:selected').text());
  $richActiveText.appendTo($richSelect);

  const $richMenu = $('<div class="simple-menu select__menu"></div>');

  const $menuItems = $('<ul class="list simple-menu__items"></ul>');

  $originalSelect.find('option').each((j, optionEl) => {
    const $originalOption = $(optionEl);

    const $richOption = $('<li class="list-item" role="option"></li>');

    if ($originalOption.val() !== '') {
      $richOption.attr('id', $originalOption.val());
    }
    $richOption.text($originalOption.text());

    if ($originalOption.prop('disabled')) {
      $richOption.attr('tabindex', '-1');
      $richOption.attr('aria-disabled', 'true');
    } else {
      $richOption.attr('tabindex', '0');
    }

    if ($originalOption.prop('selected')) {
      $richOption.attr('aria-selected', 'true');
    }

    $richOption.appendTo($menuItems);
  });

  $menuItems.appendTo($richMenu);

  $richMenu.appendTo($richSelect);

  $richSelect.insertAfter($originalSelect);

  const activeRichSelect = new Select($richSelect[0]);

  activeRichSelect.changeHandler = (event) => {
    let activeChoice;
    let newValue;

    if (event.type === 'Select:change') {  // Rich-select change.
      activeChoice = activeRichSelect.selectedOptions[0];

      if (typeof activeChoice !== 'undefined') {
        newValue = activeChoice.id;
      } else {
        newValue = '';
      }

      $originalSelect.val(newValue);
    } else {  // Original-select change.
      activeChoice = $originalSelect.find('option:selected');
      newValue = (activeChoice.length === 1) ? activeChoice.val() : '';

      activeRichSelect.selectedIndex = activeRichSelect.options.map(
        o => o.id  // eslint-disable-line comma-dangle
      ).indexOf(newValue);
    }
    // console.info('Selected value', value);
  };

  activeRichSelect.listen('Select:change', activeRichSelect.changeHandler);
  $originalSelect.on('change', activeRichSelect.changeHandler);

  return activeRichSelect;
};

export { buildManagedSelects };
